.Navbar {
  z-index: 1;
}

.Mobile-navbar {
  display: none;
}

.Desktop-navbar,
.Nav-buttons-left-side,
.Nav-button,
.Nav-button-icon {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
}

.Desktop-navbar {
  width: 918px;
  padding-left: 41px;
  padding-right: 41px;
  height: 34px;
  margin-bottom: 20px;
  -ms-flex-pack: justify;
      justify-content: space-between;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#e67300), to(#ff9f1e));
  background-image: -webkit-linear-gradient(#e67300, #ff9f1e);
  background-image: -o-linear-gradient(#e67300, #ff9f1e);
  background-image: linear-gradient(#e67300, #ff9f1e);
}

.Nav-button {
  width: 195px;
  height: 34px;
  -ms-flex-align: center;
      align-items: center;
  color: white;
}

.Nav-button:hover {
  background: #ff9100;
  cursor: pointer;
}

.Nav-button-icon {
  width: 34px;
  height: 34px;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background-color: #a6630a;
}

.Nav-button-text {
  margin-left: 10px;
  margin-right: 10px;
}

.Nav-list-wrapper {
  -webkit-box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.75);
          box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.75);
  background: #ff9100;
}

.Nav-list {
  z-index: 1;
  list-style: none;
  margin: 0;
  padding-left: 5px;
  color: white;
}

.Nav-list li {
  padding-top: 5px;
}

.Nav-list li:last-child {
  padding-bottom: 5px;
}

.Nav-list li:hover {
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.2);
}

.Input-options {
  height: 34px;
  margin-left: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.Search-input,
.Search-select {
  margin: auto;
  height: 24px;
  padding: 1px;
  padding-left: 10px !important;
  padding-right: 10px !important;
  border: none;
  border-radius: 50px;
  background-color: rgba(255, 255, 255, 0.3);
  color: white;
  font-size: 1rem;
  outline-style: none;
}

.Search-input {
  padding-left: 5px;
}

.Search-select {
  padding-top: 2px;
  padding-bottom: 2px;
  padding-left: 5px;
  padding-right: 5px;
}

.Search-input::-webkit-input-placeholder {
  color: white;
  opacity: 1;
}

.Search-input:-ms-input-placeholder {
  color: white;
  opacity: 1;
}

.Search-input::-ms-input-placeholder {
  color: white;
  opacity: 1;
}

.Search-input::placeholder {
  color: white;
  opacity: 1;
}

.Search-select option {
  color: black;
}

@media only screen and (max-width: 1000px) {
  .Desktop-navbar {
    display: none;
  }

  .Mobile-navbar {
    display: block;
  }

  .Navbar {
    width: 100%;
  }

  .Mobile-navbar {
    width: 100%;
    height: auto;
    padding: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .Nav-button {
    width: 100%;
    font-size: 1rem;
    background-image: -webkit-gradient(linear, left top, left bottom, from(#e67300), to(#ff9f1e));
    background-image: -webkit-linear-gradient(#e67300, #ff9f1e);
    background-image: -o-linear-gradient(#e67300, #ff9f1e);
    background-image: linear-gradient(#e67300, #ff9f1e);
  }

  .Nav-button,
  .Nav-button-icon,
  .Search-input,
  .Search-select {
    height: 26px;
    font-size: 0.9rem;
  }

  .Nav-button-icon {
    width: 26px;
  }

  .Add-person-button,
  .Delete-all-button,
  .Search-dropdown-wrapper {
    margin-top: 5px;
  }

  .Add-person-button {
    -ms-flex-order: 1;
        order: 1;
  }

  .Delete-all-button {
    -ms-flex-order: 2;
        order: 2;
  }

  .Search-dropdown-wrapper {
    -ms-flex-order: 3;
        order: 3;
  }

  .Search-input,
  .Search-select {
    -ms-flex-order: 4;
        order: 4;
    margin-left: 0;
    margin-right: 0;
    /*background-image: linear-gradient(#ff9f1e, #e67300);*/
    background-color: #ff9f1e;
    border-radius: 0;
    border: none;
  }

  .Search-input {
    width: 98%;
    padding-right: 1% !important;
    padding-left: 1% !important;
  }

  .Search-select {
    width: 100%;
  }
}



