.Add-form-cover {
	position: fixed;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	margin: auto;
	z-index: 2;
	background-color: rgba(0, 0, 0, 0.5);
}

.Add-form-wrapper {
	position: absolute;
	left: 25%;
	right: 25%;
	top: 25%;
	width: 500px;
	height: 210px;
	margin: auto;
	background-color: #2179bc;
	-webkit-box-shadow: 0px 0px 20px 8px rgba(0, 0, 0, 0.75);
	        box-shadow: 0px 0px 20px 8px rgba(0, 0, 0, 0.75);
	border-radius: 5px;
	color: white;
	text-shadow: 1px 1px black;
	padding: 20px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}

.Instructions {
	height: 49%;
	margin: 10px;
}

.Add-form {
	height: 49%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}

.Add-form-inputs {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}

.Add-form-inputs label {
	width: 49%;
	margin-bottom: 5px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-wrap: nowrap;
	    flex-wrap: nowrap;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}

.Add-form-inputs label input::-webkit-input-placeholder {
	color: red;
}

.Add-form-inputs label input:-ms-input-placeholder {
	color: red;
}

.Add-form-inputs label input::-ms-input-placeholder {
	color: red;
}

.Add-form-inputs label input::placeholder {
	color: red;
}

.Gender-error {
	color: red;
	font-size: 0.75rem;
}

.Button-wrap {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: center;
	    justify-content: center;
}

.Form-button {
	width: 75px;
	height: 25px;
	padding: 0px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-wrap: nowrap;
	    flex-wrap: nowrap;
	background-image: -webkit-gradient(linear, left top, left bottom, from(#e67300), to(#ff9f1e));
	background-image: -webkit-linear-gradient(#e67300, #ff9f1e);
	background-image: -o-linear-gradient(#e67300, #ff9f1e);
	background-image: linear-gradient(#e67300, #ff9f1e);
	border: none;
	color: white;
	-webkit-box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, 0.75);
	        box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, 0.75);
}

.Form-button:hover {
	background: #ff9100;
	cursor: pointer;
}

.Form-button:first-child {
	margin-right: 30px;
}

.Form-button-icon {
	width: 25px;
	background-color: #a6630a;
}

.Form-button-icon,
.Form-button-text {
	height: 25px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

.Form-button-text {
	width: 50px;
}

@media only screen and (max-width: 1000px) {
	.Add-form-wrapper {
		width: 90%;
		left: 5%;
		right: 5%;
		padding: 0;
		height: auto;
	}

	.Add-form-inputs {
		-ms-flex-direction: column;
		    flex-direction: column;
	}

	.Add-form-inputs label {
		width: 80%;
		margin: auto;
		margin-bottom: 5px;
	}

	.Add-form-inputs label input {
		width: 60%;
	}

	.Button-wrap {
		margin-top: 5px;
		margin-bottom: 10px;
	}
}



